    show_friends(num) {
        const index = num;
        // this.garlic.visible = false;
        // this.friend_group.visible = true;
        const pos = this.positions[index];

        for (let i = 0; i < index + 1; i++) {
            this.friend_group.children[i].scale.set(1, 1, 1);
            if (index > 0) {
                this.friend_group.children[i].scale.set(this.friend_scale, this.friend_scale, this.friend_scale);
            }
            this.friend_group.children[i].position.set(pos[i][0], pos[i][1], pos[i][2]);
            this.friend_group.children[i].turn2top(1000);
            this.friend_group.children[i].rotate(Math.random() * (2 * Math.PI));
            console.log(this.friend_group.children[i])
            this.friend_group.children[i].visible = true;
        }
        for (let i = index + 1; i < this.max_count; i++) {
            this.friend_group.children[i].visible = false;
        }

        // for (let friend of this.friend_group.children) {
        //     friend.turn2top(1000);
        //     friend.rotate(Math.random() * (2 * Math.PI));
        // }

        var start = { z: this.friend_group.rotation.z };
        var target = { z: Math.PI * 20 };
        new TWEEN.Tween(start)
            .to(target, 100000)
            .onUpdate(() => {
                this.friend_group.rotation.z = start.z;
            })
            .start();
    }

